import React, { useState } from 'react';
import './App.css';

function App() {
const [email, setEmail] = useState('');
const [rememberDevice, setRememberDevice] = useState(true);
const [loggedIn, setLoggedIn] = useState(false)


const handleSubmit = (e) => {
  e.preventDefault();
  setLoggedIn(!loggedIn)
}



const handleChange = (e) => {
  setRememberDevice(!rememberDevice);
}

  return (
    <div className="login-container">
      <form className="contact-form" onSubmit={handleSubmit}>
      <img src="https://green.cdn.energy/branding/logo-r.svg" alt="logo" />
      <h1>Example login screen</h1>
      <h4>Getting started with Green</h4>
      <div className="inputs">
      {!loggedIn && <> <label for="email">Email Address</label>
      <input 
      className="email-input"
      type="text"
      id="email"
      name="email"
      placeholder=""
      value={email}
      onChange={(e) => setEmail(e.target.value)}
      required
      /> </>} 
      {loggedIn && <h4>Welcome {email}</h4>}
      {loggedIn && rememberDevice && <h4>We have remembered your device</h4>}
      {!loggedIn && <div>
      <input
      className="checkbox"
      type="checkbox"
      id="device"
      checked= {rememberDevice}
      value={rememberDevice}
      onChange={(e) => handleChange()}
      />
      <label for="device">Remember this device</label>
      </div>} 
      <button type="submit" className="btn">{!loggedIn && 'Sign In'}{loggedIn && 'Sign Out'}</button>
                </div>
      </form>
    </div>

  );
}

export default App;
